import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { BuyDataPage } from '../pages/buy-data/buy-data';
import { ChangePasswordPage } from '../pages/change-password/change-password';
import { CheckBalancePage } from '../pages/check-balance/check-balance';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { LoginHistoryPage } from '../pages/login-history/login-history';
import { SettingsPage } from '../pages/settings/settings';
import { ShareDataPage } from '../pages/share-data/share-data';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    BuyDataPage,
    ChangePasswordPage,
    CheckBalancePage,
    LoginPage,
    LoginHistoryPage,
    SettingsPage,
    ShareDataPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/buy-data/buy-data.module#BuyDataPageModule', name: 'BuyDataPage', segment: 'buy-data', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/change-password/change-password.module#ChangePasswordPageModule', name: 'ChangePasswordPage', segment: 'change-password', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/check-balance/check-balance.module#CheckBalancePageModule', name: 'CheckBalancePage', segment: 'check-balance', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login-history/login-history.module#LoginHistoryPageModule', name: 'LoginHistoryPage', segment: 'login-history', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module#SettingsPageModule', name: 'SettingsPage', segment: 'settings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/share-data/share-data.module#ShareDataPageModule', name: 'ShareDataPage', segment: 'share-data', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    BuyDataPage,
    ChangePasswordPage,
    CheckBalancePage,
    LoginPage,
    LoginHistoryPage,
    SettingsPage,
    ShareDataPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



